@import url(https://fonts.googleapis.com/css2?family=Jura&family=Rubik+Mono+One&family=Russo+One&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
body {
  background-color: #FFDF8C;
  padding: 43px 0;
}

.App {
  border-radius: 10px;
  background-color: #fff;
  text-align: center;
  max-width: 1340px;
  padding: 0 60px;
  margin: auto;
}
.header_wrapper {
  padding-top: 50px;
  height: 12vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}
  
.logo {
  position: absolute;
  margin: 15px 0 15px 0;
  top: -50px;
  left: -50px;
  height: 180px;
}
  
.basket {
  width: 150px;
  height: 50px;
  background-color: #FE5F1E;
  border-radius: 30px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.total_count span {
  color: #fff;
}

.bask {
  width: 35px;
}
  
@media screen and (max-width: 500px) {
    .logo {
        margin-left: 5%;
        height: 50px;
    }

    .contacts {
        flex-wrap: wrap;
        width: 45%;
    }

    a {
        font-weight: bold;
        color:red;
        text-decoration: none;
    }

    .contact {
        display: none;
    }
}
.navbar {
  margin-left: 10%;
  margin-top: 30px;
  margin-bottom: 30px;
}

.nav_btn {
  padding: 15px 25px;
  font-size: 22px;
  font-family: 'Rubik Mono One', sans-serif;
  color: #db082e;
  text-decoration: none;
  border: none;
  margin-right: 10px;
  cursor: pointer;
  transition: .3s;
}

.nav_btn:focus {
  outline: none;
  color: #FE5F1E;;
}

@media screen and (max-width: 500px) {
  .navbar {
    margin-top: 30px;
  }
  
  .nav_btn {
    padding: 5px 15px;
  }
}
